html, body {
  min-height:100%;
  height:100%
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
#root, .App {
  height:100%
}

.App {
  min-width: 640px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-input {
  outline: none;
  margin-right: 8px;
  padding: 4px;
  border-radius: 2px;
  max-width: 100%;
}
.App-addBtn {
  position: relative;
  top: 8px;
}

.App-handles {
  background-color: #222;
  width: 30%;
  padding: 20px;
  color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: auto;
  overflow-x: hidden;
}

.App-map {
  width: 70%;
}

.App-itemTitle {
  display: inline-block;
  max-width: calc(100% - 48px);
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: 0.9em;
}

.App-item {
  list-style: none;
  position: relative;
  text-align: left;
}

.App-flyingItem .mdc-icon-button {
  visibility: hidden;
}

.App-listPoints {
  padding-left: .8em;
}


